function resolveWixOnlineprogramsProgramsV3ProgramsServiceUrl(
  opts: Omit<ResolveUrlOpts, 'domainToMappings'>
) {
  const domainToMappings = {
    'manage._base_domain_': [
      {
        srcPath: '/_api/programs-service',
        destPath: '',
      },
    ],
    _: [
      {
        srcPath: '/_api/programs-service',
        destPath: '',
      },
    ],
    'editor.wixapps.net': [
      {
        srcPath: '/_api/programs-service',
        destPath: '',
      },
    ],
    'www.wixapis.com': [
      {
        srcPath: '/_api/programs-service',
        destPath: '',
      },
    ],
  };

  return resolveUrl(Object.assign(opts, { domainToMappings }));
}

